.star {
  display: flex;

  &__stars {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url(/images/star-active.svg);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.star--4 .star__stars:last-child {
  background-image: url(/images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}
